import React, { Component } from 'react';
import { Row, Col, Menu, Icon } from 'antd';
import './style.scss';

class MenuTabTop extends Component {
  state = {
    current: 'printTranscript',
  }
  handleClick = (e) => {
    console.log('click ', e);
    this.setState({
      current: e.key,
    });
  }
  render() {
    return (
      <Row>
        <Col span={24}>
          <Menu
            onClick={this.handleClick}
            selectedKeys={[this.state.current]}
            mode="horizontal"
          >
            <Menu.Item key="printTranscript">
              <Icon type="printer" /> In bảng điểm
            </Menu.Item>
            <Menu.Item key="printTranscriptEn">
              <Icon type="printer" /> Bảng điểm tiếng Anh
            </Menu.Item>
            <Menu.Item key="fileExcel">
              <Icon type="file-excel" /> Xuất Excel
            </Menu.Item>
            <Menu.Item key="stop">
              <Icon type="stop" theme="twoTone" twoToneColor="red" />
              Thoát
            </Menu.Item>
          </Menu>
        </Col>
      </Row>
    );
  }
}

export default MenuTabTop;



